root {
  background-color: #f5f5f5;
}

.container {
  display: flex;
  flex-wrap: wrap;
  flex-direction: column;
  align-content: center;
}

#editor, #preview {
  width: 70%;
  margin: 0px 0px 10px;
  padding: 15px;
  box-shadow: 0px 10px 10px gray;
  border-radius: 3px;
  border: 0;
  min-width: 350px;
}

textarea {
  resize: vertical;
}

#containerHeading {
  color: gray;
  text-align: center;
  filter: drop-shadow(0px 0px 10px lightgray);
  margin: 35px 0px 15px;
}

.heading {
  color: gray;
  filter: drop-shadow(0px 0px 10px lightgray);
  margin-bottom: 0;
  padding-left: 10px;
}

blockquote p::before {
  content: open-quote;
  font-size: 1.5em;
}

code {
  background-color: #f5f5f5;
  padding: 0px 2px;
}

pre {
  width: 100%;
  background-color: #f5f5f5;
  padding: 5px;
}